{
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;

}

input {
  width: 100%;
  font-size: 1.5rem;
  color: blue;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 100px;
  margin-right: 100px;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}